import axios from 'axios';
import { getIndexedDB } from '../utils/indexeddb';
import { i18n } from '@/i18n';

export default {
  namespaced: false,
  state: {
    title: null,
    readyState: 0,          // for sync loaded
    pendingKeys: new Set(),
    updated: null,
  },
  getters: {
    messages: (state) => state.messages,
    i18nReady: (state) => state.readyState === 4,
    i18nUpdated: (state) => state.updated,
    updateTimeout: (state, rootGetters) => {
      return rootGetters.env.VUE_APP_MODE === 'development'
        ? 0
        : 30 * 24 * 60 * 60 * 10000;
    },
  },
  mutations: {
    title(state, title) {
      if (title) state.title = title; // old or new title

      const newTitle = (typeof state.title === 'function')
        ? state.title()
        : state.title || i18n.t('global.title');
      if (global.document.title === newTitle) return;
      global.document.title = newTitle;

      const locale = i18n.locale;
      document.querySelector('html').setAttribute('lang', locale);

      ['title', 'description', 'keywords'].forEach((name) => {
        document
          .querySelectorAll(`meta[name$="${name}"], meta[property$="${name}"]`)
          .forEach((el) => {
            const attr = el.hasAttribute('property') ? 'property' : 'name';
            const fullname = el.getAttribute(attr);
            const value =
              global.document[name] ||
              (i18n.te(`global.${fullname}`)
                ? i18n.t(`global.${fullname}`)
                : i18n.t(`global.${name}`));

            if (el.getAttribute('content') === value) return;
            el.setAttribute('content', value);
          });
      });
    },
    async localeChanged(state) {
      state.readyState = 0;
      state.pendingKeys.clear();
      await this.dispatch('fetchI18n');
    },
  },
  actions: {
    // start from indexeddb
    async fetchI18n({ dispatch, state, rootGetters }, data = {}) {
      const { key, locale = i18n.locale } = data;
      const messages = i18n.messages[locale];
      const getKey = (messages, key) => key.split('.').reduce((res, key) => res?.[key], messages);

      if (messages) {
        if (messages.__source === 'remote') return; // no source for update
        // already set
        if (!key || getKey(messages, key)) {
          state.readyState = 4;
          state.pendingKeys.clear();
          return;
        }
        // set indexeddb, need to update from the remote source
        if (key && messages.__source !== 'remote') {
          if (state.readyState !== 1) dispatch('downloadI18n', data);
          return;
        }
        // need set or update
      }
      key && state.pendingKeys.add(key);

      // in proccess
      if (state.readyState === 1) return;


      state.readyState = 1;
      dispatch('fetchI18nIdDB')
        .then((e) => {
          const { messages, updated } = e.target.result || {};
          if (!(messages && [...state.pendingKeys].every((key) => getKey(messages, key)))) {
            return Promise.reject(e);
          }

          messages.__source = 'indexeddb';
          i18n.messages[locale] = messages;
          state.updated = updated;

          state.readyState = 4;
          state.pendingKeys.clear();

          if (Date.now() - state.updated > rootGetters.updateTimeout)
            return Promise.reject(e);
        })
        .catch((error) => {
          console.info(`Can't fetch i18n.messages.${locale}${ key ? `.${key}` : ''} from indexeddb.\n`, error);
          // need download
          dispatch('downloadI18n', data);
        });
    },
    // from remote source
    async downloadI18n({ dispatch, state, rootGetters }, data) {
      const locale = data?.locale || i18n.locale;
      if (i18n.messages[locale]?.__source === 'remote') {
        state.readyState = 4;
        return;
      }

      state.readyState = 1;
      let messages = {}; // must by
      return axios({
        method: 'get',
        url: `locales/${locale}.json`,
        baseURL: rootGetters.env.BASE_URL,
      })
        .then((response) => {
          messages = response.data;
          dispatch('syncI18nDB', { locale, messages });
        })
        .catch(function (error) {
          console.info(`Can't fetch i18n.messages.${locale} from remote source.\n`, error);
        })
        .finally(() => {
          messages.__source = 'remote';
          i18n.messages[locale] = messages;
          state.updated = Date.now();

          state.readyState = 4;
          state.pendingKeys.clear();
        });
    },
    async fetchI18nIdDB({ state }, locale = i18n.locale) {
      state.indexedDB = getIndexedDB();
      return state.indexedDB.then(function (db) {
        return db.store('i18n').get(locale);
      });
    },
    async syncI18nDB({ state }, { locale, messages }) {
      if (!state.indexedDB) return;

      return state.indexedDB.then((db) => {
        return db.store('i18n').set({
          locale,
          messages,
          updated: Date.now(),
        });
      });
    },
  },
};
