import { createApp } from 'vue';
import BootstrapVueNext from 'bootstrap-vue-next';

import router from './router';
import I18n from './i18n';
import store from './store';
import App from './App.vue';
import { speechPlugin } from './components/Speech/Speech';

const isTouchDevice = global.isTouchDevice =
  'ontouchstart' in window ||
  navigator.maxTouchPoints > 0 ||
  navigator.msMaxTouchPoints > 0;

// Cannot use keyword 'await' outside an async function
const loadForTouch = async () => {
  await import(`/src/assets/styles/main${isTouchDevice ? '-touch': ''}.scss`);
  isTouchDevice && await import('drag-drop-touch/DragDropTouch.js');
};
loadForTouch();

const app = createApp(App);
app.use(I18n);
app.use(router);
app.use(BootstrapVueNext);

app.use(speechPlugin);
app.use(store);

app.mount('#app');
