<template>
  <!-- Training -->
  <b-row
    :class="`main training-layout training-layout-${trainingType} ${Object.keys(
      $store.getters.settings.training.mode
    )
      .map((mode) => $store.getters.settings.training.mode[mode] ? 'training-mode-' + mode : '')
      .join(' ')}`"
  >
    <!-- Actions button -->
    <b-col
      class="col-left"
      v-show="
        currentPos <= countPos &&
        (isLoaded
          ? !(alert && alert.name === 'noneItems')
          : alert && alert.name === 'noneNetwork')
      "
    >
      <training-actions
        v-show="item"
        :isBulk="true"
        :item="item"
        :actions="['card', 'picking', 'docking', 'writing', 'article', 'score']"
      ></training-actions>
    </b-col>
    <!-- Main content -->
    <b-col class="col-main">
      <training-bulk></training-bulk>
      <!-- Buttons -->
      <div
        class="training-buttons"
        v-show="isLoaded && !(alert && alert.name !== 'noneArticle')"
      >
        <b-button
          :class="`training-button-${name}`"
          :name="name"
          :variant="button.variant || name"
          :disable="button.disable"
          v-show="button.visible"

          v-for="(button, name) in buttons"
          :key="name"
          @click="onPressButton"
          :ref="name"
        >
          {{ this.$t(`buttons.${button.type}`) }}
        </b-button>
      </div>
    </b-col>
    <b-col class="col-right" v-show="isLoaded && !alert">
      <listening-settings />
      <complexity-settings />
    </b-col>
  </b-row>
</template>

<script>
import TrainingActions from '@/components/Training/Actions';
import TrainingBulk from '@/views/Training/Bulk';
import ComplexitySettings from '@/components/Settings/Complexity';
import ListeningSettings from '@/components/Settings/Listening';

export default {
  name: 'Training Layout',
  components: {
    TrainingActions,
    TrainingBulk,
    ComplexitySettings,
    ListeningSettings,
  },
  provide() {
    return {
      buttons: this.buttons,
    };
  },
  data() {
    const data = {
      buttons: {
        left: {
          pressed: 0,
          type: 'results',
          visible: true,
          disable: false,
          variant: 'outline-secondary',
        },
        // note: the right button is used for score.exam
        right: {
          pressed: 0,
          type: 'continue',
          visible: true,
          disable: false,
        },
        set: this.setButtons,
        elem: this.getButtonElem,
      },
    };
    Object.defineProperties(data.buttons, {
      set: { enumerable: false },
      elem: { enumerable: false },
    });
    return data;
  },
  computed: {
    isLoaded() {
      return this.$store.getters.isLoaded;
    },
    alert() {
      return this.$store.getters.alert;
    },
    currentPos() {
      return this.$store.getters.indexTrainingItem + 1;
    },
    countPos() {
      return this.$store.getters.bulkLength;
    },
    item() {
      return this.$store.getters.trainingItem;
    },
    trainingType() {
      return this.$store.getters.trainingType;
    },
  },
  beforeCreate() {
    this.$store.commit('loaded', { bulk: false });
  },
  mounted() {
    window.addEventListener('keydown', this.onKeydown);
  },
  unmounted() {
    window.removeEventListener('keydown', this.onKeydown);
    this.$store.commit('loaded', { bulk: undefined });
  },
  methods: {
    onKeydown(e) {
      if (document.querySelector('.modal.show, .offcanvas.show')) return;
      if (e.code === 'Enter') {
        e.preventDefault();
        this.buttons
          .elem(this.buttons.right.visible ? 'right' : 'left')
          .click();
      }
      if (e.code === 'Escape') {
        e.preventDefault();
        this.buttons.elem('left').click();
      }
      if (
        e.altKey &&
        this.item &&
        e.code === 'KeyS' &&
        (this.$store.getters.isSuccess || this.$store.getters.listening.on)
      ) {
        e.preventDefault();
        this.$speak(this.item);
      }
    },
    onPressButton(e) {
      this.buttons.set({
        [e.currentTarget.name]: { pressed: Date.now() },
      });
    },
    setButtons(buttons) {
      Object.keys(buttons).forEach((key) => {
        const button = this.buttons[key];
        Object.keys(buttons[key]).forEach((prop) => {
          button[prop] = buttons[key][prop];
        });
        if (!buttons[key].variant) {
          switch (button.type) {
            case 'incorrect':
            case 'skip':
              button.variant = 'danger';
              break;
            case 'restart':
            case 'continue':
            case 'check':
            case 'correct':
              button.variant = 'success';
              break;
            default:
              button.variant = 'outline-secondary';
          }
        }
      });
    },
    getButtonElem(name) {
      return this.$refs[name][0].$el;
    },
  },
};
</script>
