export default {
  namespaced: false,
  state: {
    summary: 0,
    day: new Date().setHours(0, 0, 0, 0),
    fullSummary: undefined,
    shown: false,
  },
  getters: {
    summary: (state) => state.summary,
    shownDaySummary: (state) => state.shown,

    fullSummary(state, rootGetters) {
      if (state.fullSummary === undefined)
        state.fullSummary = Math.round(
          rootGetters.items.reduce((sum, item) => sum + item.score.value, 0)
        );
      return state.fullSummary + state.summary;
    },
    unfoldStore: () => ({ commit, dispatch, rootGetters }, item) => {
        const ratio = rootGetters.settings.score.ratio;
        const data = item.score?.data || {
          card: 0,
          docking: 0,
          writing: 0,
          picking: 0,
        };
        const lengthFactor =
          item.text.length > 30 ? 1.5 : item.text.length < 5 ? 0.9 : 1;

        item.score = {
          data,
          toString() {
            return this.formatted;
          },
          valueOf() {
            return this.value;
          },
          get value() {
            return Object.keys(this.data).reduce(
              (sum, name) => sum + this.data[name] * ratio[name],
              0
            );
          },
          get formatted() {
            return Number(this.value).toFixed(2);
          },
          get info() {
            return Object.keys(this.data).map((name) => {
              return {
                name,
                value: Number(this.data[name] * ratio[name]).toFixed(2),
              };
            });
          },
          clear() {
            const val = this.value;
            Object.keys(this.data).forEach((name) => {
              this.data[name] = 0;
            });
            commit('summary', -val);
            dispatch('syncDictionaryDB', item);
          },
          exam({ name, result, score = 1 }) {
            /* result: {'success' || 'skip' || 'error'} */
            if (result === 'success') {
              // for length
              score *= lengthFactor;
              // for complexity trainings
              score *= rootGetters.settings.training.complexity[name] || 1;
              score *= rootGetters.settings.training.mode.listening ? 2 : 1;
            } else {
              // now it's only for picking 1/n
              score *= rootGetters.settings.training.mode.listening ? 0.5 : 1;
              score = -score;
            }

            score = this.data[name] + score;
            score = score > 0 ? score : 0;

            if (this.data[name] === score) return;

            this.data[name] = score;
            commit('summary', score);
            dispatch('syncDictionaryDB', item);
          },
          set formatted(val) {
            // trash && sodomy
            // check: info: Uncaught (in promise) TypeError: Cannot set property value of #<Object> which has only a getter at eval (bootstrap-vue-next.es.js?9bd3:185:1)
          },
          set value(val) {
            // trash && sodomy
            // check: info: Uncaught (in promise) TypeError: Cannot set property value of #<Object> which has only a getter at eval (bootstrap-vue-next.es.js?9bd3:185:1)
          },
          set info(val) {
            // trash && sodomy
            // check: info: Uncaught (in promise) TypeError: Cannot set property value of #<Object> which has only a getter at eval (bootstrap-vue-next.es.js?9bd3:185:1)
          },
        };
        return item.score;
      },
  },
  mutations: {
    summaryInit: (state) => {
      const { day, summary, shown } = JSON.parse(window.localStorage.getItem('day-summary')) || {};
      if (day !== new Date().setHours(0, 0, 0, 0)) return;
      state.summary = summary;
      state.shown = shown;
    },
    shownDaySummary: (state, shown) => {
      state.shown = shown;
    },
    summary: (state, score = 0) => {
      score = state.summary + score;
      if (score < 0) return;
      state.summary = score;

      window.localStorage.setItem(
        'day-summary',
        JSON.stringify({ summary: score, day: state.day, shown: state.shown })
      );
    },
  },
};
