<template>
  <b-button
    v-show="$store.getters.listening.available"
    :class="`btn-listening`"
    variant="outline-secondary"
    :title="$t('settings.training.listening')"
    :active="listening.on"
    @click="$store.getters.settings.training.mode.listening = !listening.on"
  >
    <iconify-icon :icon="`lucide:ear${listening.on ? '' : '-off'}`" />
  </b-button>
</template>

<script>
import { Icon as IconifyIcon } from '@iconify/vue';

export default {
  name: 'Setting for training',
  components: {
    IconifyIcon,
  },
  computed: {
    listening() {
      return this.$store.getters.listening;
    }
  },
};
</script>
