<template>
  <iconify-icon
    icon="gg:sort-az"
    :class="`dictionary-sort-icon${active ? ' active' : ''}`"
    :rotate="active && sort.desc ? 0 : 2"
    @click="$emit('click', field)"
  ></iconify-icon>
</template>

<script>
import { Icon as IconifyIcon } from '@iconify/vue';
export default {
  name: 'Dictionary',
  props: {
    field: String,
    sort: Object,
  },
  emits: ['click'],
  components: {
    IconifyIcon,
  },
  computed: {
    active() {
      return this.sort.by === this.field;
    },
  },
};
</script>
