export default {
  namespaced: false,
  state: {
    env: {},
  },
  getters: {
    env: (state) => state.env,
  },
  mutations: {
    initENV(state) {
      const localENV = JSON.parse(window.localStorage.getItem('process.env')) || {};
      state.env = Object.assign(state.env, process.env, localENV);

      const VUE_MODE = global.VUE_MODE = process.env.VUE_APP_MODE;
      global[`is${VUE_MODE[0].toUpperCase()}${VUE_MODE.substring(1).toLocaleLowerCase()}`] = true;
    },
  },
};
