export default {
  namespaced: false,
  state: {
    listening: {},
  },
  getters: {
    listening: (state, rootGetters) => {
      const { voices, trainingItem } = rootGetters;
      const trainingSettings = rootGetters.settings.training;
      const on = trainingSettings.mode.listening;
      let available = false;
      if (voices.langs.length) {
        if (trainingItem) available = !!voices[trainingItem.lang];
        else if (trainingSettings.lang)
          available = !!voices[trainingSettings.lang];
      }

      return {
        on,
        available,
        enabled: on && available,
      };
    },
  },
};
